* {
	font-family: ProbaPro, sans-serif;
	padding: 0;
	margin: 0;
	border: 0;
	transition: all ease-in-out .5s;
}

*,
:after,
:before {
	box-sizing: border-box
}

:active,
:focus {
	outline: 0
}

html,
body
 {
	font-size: 100%;
	line-height: 1;
	scroll-behavior: smooth;
}

body
 {
 	overflow-x: hidden;
}

body section {
 	overflow: hidden;
}

aside,
footer,
