.map {
	position: relative;
	min-height: 80px;
}

.map * {

	transition: all linear .8s;
}

.map .map__toggle-btn {
	width: 287px;
	height: 67px;
	position: absolute;
	top: 0;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1;
	cursor: pointer;
}

.map .map__toggle-btn div {
	letter-spacing: 2.5px;
	line-height: 13.66px;
	text-transform: uppercase;
	color: #fff;
	font-size: calc(18px + 4 * ((100vw - 320px)/ 1600));
	font-weight: 400;
	background-color: #0e539a;
	width: 100%;
	height: 100%;
	overflow: hidden;
	transition: .5s ease-out;
	display:flex;
	justify-content: center;
	align-items: center;
	position:relative;
}

.map .map__toggle-btn div:before {
	transition: 0.5s;
    z-index:-1;
    content:'';
    position:absolute;
    top:0;
    left:0;
    display:block;
    width: 100%;
    height: 100%;
    transform:translateY(100%);
	background: #ebebeb;
}

.map .map__toggle-btn:hover div {
	color: #0e539a;
	transition-delay: .1s;
	z-index:8;
	background-color: #ebebeb;
}
.map .map__toggle-btn:hover div:before {
	transform:translateY(0);
}

