	top: 0;
	left: 0
}

.popup .popup__modal {
	width: 477px;
	height: 519px;
	background-color: #fff;
	padding: 50px 56px 95px;
	position: relative
}

.popup .popup__modal .modal__title {
	font-size: calc(20px + 10 * ((100vw - 320px)/ 1600));
	font-weight: 700;
