.page-informtion {
	margin: 0 0 22px
}

.avail {
	margin: 0 0 93px
}

.avail .avail__fix-content .avail__tumbler .tumbler__item {
	padding: 8px 20px;
	color: #111227;
	font-size: calc(17px + 9 * ((100vw - 320px)/ 1600));
	font-weight: 400;
	line-height: 28px;
	letter-spacing: .78px;
	text-transform: uppercase;
	background-color: transparent;
	margin: 0 50px 0 0
}

.avail .avail__fix-content .avail__tumbler .tumbler__item:hover {
	background-color: #28bd49;
	color: #fff
}

.avail .avail__fix-content .avail__tumbler .tumbler__item.active {
	background-color: #28bd49;
	color: #fff
}

.avail .avail__fix-content .avail__content {
	margin: 100px 0 0;
	position: relative;
}

.avail .avail__fix-content .avail__content .content__item.active {
	position: relative;
	top: 15%;
	left: 0;
	transform: translate(0,0);
}

.avail .avail__fix-content .avail__content .content__item {
	position: absolute;
	top: 15%;
	left: 0;
	transform: translate(-200%,0);
}

.avail .avail__fix-content .avail__content .content__item .item__title {
	color: #111227;
	font-size: calc(17px + 9 * ((100vw - 320px)/ 1600));
	font-weight: 700;
	line-height: 32px;
	text-transform: uppercase;
	text-align: center;
	margin: 47px 0 15px;
	letter-spacing: 1.5px
}

.avail .avail__fix-content .avail__content .content__item .item__subtitle {
	color: #454551;
	font-size: 20px;
	font-weight: 400;
	line-height: 28px;
	letter-spacing: .2px;
	text-align: center
}

.avail .avail__fix-content .avail__content .content__item .item_avails {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 45px 0 0
}

.avail .avail__fix-content .avail__content .content__item .item_avails .avails__item {
	width: 21.03376%;
	color: #111227;
	font-size: 16px;
	font-weight: 600;
	line-height: 24.01px;
	position: relative;
	margin: 0 20px 30px 0;
	padding: 0 0 0 25px;
}

.avail .avail__fix-content .avail__content .content__item .item_avails .avails__item:before {
	content: url(../img/avail__img2.png);
	position: absolute;
	left: 0;
	top: 8%
}

.avail .avail__fix-content .avail__content .content__item .content__article {
	width: 83.25598%;
	font-weight: 500;
	line-height: 24px;
	letter-spacing: .16px;
	font-size: 19px;
	letter-spacing: .19px;
	color: #05080c;
	padding: 21px 25px;
	text-align: center;
	margin: 9px auto
}

.avail .avail__fix-content .avail__content .content__item .content__article.bg-color {
	font-size: 16px;
	font-weight: 400;
	color: #fff;
	background-color: #0e539a
}

.desription {
	margin: 0 0 30px
}

.desription .desription__fix-content .desription__article {
	color: #111227;
	font-size: 16px;
	font-weight: 400;
	line-height: 28px;
	letter-spacing: .48px
}

@media (max-width:1436px) {
	.avail .avail__fix-content .avail__content .content__item .content__article {
		width: 100%
	}
}

@media (max-width:1402px) {
	.avail .avail__fix-content .avail__content .content__item .item_avails .avails__item {
		width: 45%
	}
}

@media (max-width:799px) {
	.avail .avail__fix-content .avail__tumbler {
		display: flex;
		justify-content: space-between
	}
	.avail .avail__fix-content .avail__tumbler .tumbler__item {
		width: 45%
	}
	.avail .avail__fix-content .avail__content .content__img {
		height: 100px;
		-o-object-fit: cover;
		object-fit: cover
	}
	.avail .avail__fix-content .avail__content .content__item .item_avails {
		justify-content: center;
		padding: 0 0 0 3%
	}
	.avail .avail__fix-content .avail__content .content__item .item_avails .avails__item {
		width: 100%;
		text-align: center
	}
}

@media (max-width:370px) {
	.avail .avail__fix-content .avail__content .content__item .item_avails .avails__item {
		width: 80%
	}
}